
.card-modal-screen::-webkit-scrollbar {
    width: 15px;
    height: 10px;
    position: absolute;
    z-index: 0;
    
}
/* Track */
.card-modal-screen::-webkit-scrollbar-track {
    background: rgba(9,30,66,.08);; 
    border-radius: 8px;
    
}
/* Handle */
.card-modal-screen::-webkit-scrollbar-thumb {
    background: rgba(190, 190, 189, 0.788);
    border-radius: 8px;
    
    // border
    
  }



.card-modal-screen {
    scroll-behavior: smooth;
    min-height: 100vh;
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.64);
    align-items: flex-start;
    height: 100%;
    overflow-y: auto;
    width: 100%;
    h3 {
        font-size: 1em;
        font-weight: 600;
        line-height: 20px;
        color: $clr2;
        padding-right: 8px;
        margin-bottom: 30px;
    }
    .card-modal {
        border-radius: 3px;
        min-height: 85vh;
        margin: 50px 0px 80px;
        padding: 16px;
        background-color: #f4f5f7;
        overflow: hidden;
        position: relative;
        z-index: 25;
        grid-template-columns: 570px 170px;
        grid-template-rows: 60px minmax(500px, auto);
        @media (max-width: 800px) {
            grid-template-columns: 400px;
            grid-template-rows: 30px 30px 1px minmax(calc(100vh - 100px), auto);
            .modal {
                height: 0px;
            }
            input[type="checkbox"] {
                &:checked + .card-action-menu {
                    height: fit-content;
                }
            }
        }
        @media (max-width: 450px) {
            grid-template-columns: 300px;

        }
    }
    .header {
        grid-row: 1/1;
        grid-column: 1/-1;
        a {
            height: 40px;
        }
        @media (max-width: 800px) {
            grid-row: 2/3;
            grid-column: 1/-1;
            a.close-card {
                position: relative;
                top: -43px;
            }
        }
    }
    .card-content {
        grid-row: 2/-1;
        @media (max-width: 800px) {
            grid-row: 4/-1;
            grid-column: 1/1;
        }
    }
    .open-card-menu-btn {
        display: none;
        @media (max-width: 800px) {
            display: block;
            grid-row: 1/2;
            grid-column: 1/-1;
        }
    }
    .card-action-menu {
        grid-row: 2/-1;
        grid-column: 2/-1;
        @media (max-width: 800px) {
            grid-row: 3/4;
            grid-column: 1/-1;
            height: 0;
            overflow: hidden;
            background-color: $main-bgc;
            transition: height 0.3s; 
            &.open {
                padding-top: 20px;
                height: calc(100vh - 150px);
                z-index: 20;
            }
        }
    }
    .card-content > * {
        header {
            margin-top: 20px;
        }
    }
    .header {
        input {
            width: 100%;
            height: 1.5em;
            font-size: em(20px);
            color: $clr2;
            border: 0;
            background: transparent;
        }
        :not(:last-child).MuiSvgIcon-root {
            margin-top: 3px;
            margin-right: 8px;
        }
        input + a {
            position: relative;
            top: -0.8em;
            left: 0.8em;
        }
        a .MuiSvgIcon-root {
            font-size: 3em;
            padding: 10px;
            border-radius: 50%;
        }
        a .MuiSvgIcon-root:hover {
            background-color: $sec-bgc;
        }
    }
    .card-members {
        margin-right: 40px;

        div {
            margin-right: 4px;
        }
        .add-user-button {
            .MuiAvatar-root {
                background-color: $light-bgc;
                color: $clr2;
            }
            .MuiAvatar-root:hover {
                background-color: $sec-bgc;
            }
        }
    }
}

.card-action-menu {
    max-height: em(600px);
    span {
        border-radius: 3px;
        background-color: $light-bgc;
        padding: 5px 5px;
        margin: 5px;
        margin-left: 0;
        align-content: center;
    }
    span:hover {
        background-color: $sec-bgc;
    }
    h5:not(:first-child) {
        margin-top: 20px;
    }
    .MuiSvgIcon-root {
        font-size: 1.5em;
        padding-right: 5px;
    }
}
