.card-todo-preview{
    padding: 10px 0;

    .hidden-actions-form-container{
        width: 95%;
    }
    span.checkbox{
        cursor: pointer;
        color: $main-bgc;
        border: 1px solid $clr1;
        border-radius: 3px;
        line-height: 0.9em;
        margin: 3px;
        padding: 3px;
        transition: background-color 0.5s;
        font-size: em(13px);
    }
}

.card-todo-preview:hover:not(.add){
    background:$sec-bgc;
    button.icon{
        background-color: $sec-bgc;
    }
    button.icon>*{
        background-color: $sec-bgc;
    }
    span.checkbox{
        background-color: $main-bgc;

    }
    
}

.checklist-container{
    
    div.MuiLinearProgress-colorPrimary{
        margin-left: 5px;
        background-color: $sec-bgc;
        padding: 4px;
        border-radius: 2em;
    }
    div.MuiLinearProgress-barColorPrimary{
        background-color: $clr4;
    }

}