.card-content{
    padding-right: 10px;

    ul{
        list-style: none;
    }
    textarea,
    input[type="text"] {
        width: 90%;
        flex: 1;
        resize: none;
        border: 0;
        background-color: transparent;
    }
    form > div{
        width: 100%;
    }
    .card-labels{
        margin-top: 10px;
        div.label-container{
            padding-top: 5px;
        }
    }
    .card-members{
        margin-top: 10px;
    }
    .label {
        color: white;
        margin-right: 5px;
        border-radius: 4px;
        height: 2.65em;
        min-width: 5em;
        padding: 0.1em 5px;
        overflow: hidden;
        font-size: 0.8em;
        &:not(.add-label){
            font-weight: 700;
        }

    }
    div.add-label{
        background-color: $light-bgc;
        color: $clr2;
        font-size: 1.25em;
        padding: 0 em(11px);
        border-radius: 4px;
        height: 1.7em;
        &:hover{
            background-color: $sec-bgc;
        }
    }

    .MuiSvgIcon-root{
        margin-right: 10px;
    }
    
}