.menu-activities-section {
    margin: 0 10px;

    .select-active-filter {
        display: flex;
    }
    .active-filter-btn {
        font-size: 14px;
        font-weight: 400;
        background-color: $clr2;
        border-radius: 3px;
        text-align: center;
        background-color: $clr8;
        text-decoration: underline;
        padding: 10px;
        width: 50%;
    }
    .active-filter-btn:focus {
        outline: none;
    }
}
