.board-header {
    height: 50px;
    padding: 5px;
    max-width: 100%;
    color: white;
    > * > * {
        cursor: pointer;
        background-color: hsla(0, 0%, 100%, 0.47);
        border-radius: 3px;
        margin-right: 5px;
        padding: 10px;
        height: 30px;
    }

    span {
        padding: 5px;
        display: flex;
        align-items: center;
        &:hover {
            background-color: rgba(0, 0, 0, 0.18);
        }
    }
    button:hover {
        background-color: rgba(0, 0, 0, 0.18);
    }
    input {
        display: inline;
        border: 0;
        font-size: 18px;
        font-weight: 700;
        color: $clr2;
        &:focus {
            background-color: $main-bgc;
        }
        &:hover {
            background-color: rgba(0, 0, 0, 0.18);
        }
        @media (max-width: 800px) {
            display: none;
        }
    }
    .lock-icon {
        border-radius: 50%;
        color: white;
    }
    .menu-btn {
        margin: 0;
        padding: 5px 2px;
        .MuiSvgIcon-root{
            font-size: 23px;
            color: white;
        }
    }
    .members {
        background: transparent;
        padding: 0;
        .MuiAvatar-root {
            height: 30px;
            width: 30px;
            position: relative;
        }
    }
    .add-modal-screen {
        position: absolute;
        z-index: 2;
        top: 0;
        right: 0;
        left: 0;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.445);
        margin: 0;
    }
    .add-members-modal {
        background-color: white;
        top: 90px;
        left: 100px;
        height: 200px;
        box-shadow: 0px 0px 2px;
        input {
            font-size: 14px;
            font-weight: 400;
        }
    }
  
}
