.card-list{
    display: flex;
    flex-direction: column;
    width: 250px;
    overflow-y: auto;
    overflow-x: hidden;
    max-height: calc(100vh - 230px);
    @media (max-width: 570px) {
      width: 325px;
  }
    &.dragging{
      transform: rotate(6deg);
  }
}
.card-list::-webkit-scrollbar {
    width: 8px;
    height: 10px;
    position: absolute;
    z-index: 0;
  }
  /* Track */
  .card-list::-webkit-scrollbar-track {
    background: rgba(9,30,66,.08);; 
    border-radius: 8px;
  }
  /* Handle */
  .card-list::-webkit-scrollbar-thumb {
    background: rgba(190, 190, 189, 0.788);
    border-radius: 8px;
    // border
    
  }

  